<template>
  <div :class="$root.themeHomeChange === '1' ?'project-manage-formL':'project-manage-form'">
    <!--类型-按钮组-->
    <div class="manageBtn" background-color="#00AAFF">
      <!-- <bda-button @click="goBack">返回</bda-button> -->
      <bda-button @click="addDialogClick" type="primary">新增</bda-button>
      <bda-button @click="batchDelete" type="primary">批量删除</bda-button>
<!--      <bda-button @click="importDocClick">导入</bda-button>-->
      <bda-button @click="exportExcel" type="primary">导出</bda-button>
    </div>

    <!--表单-->
    <el-form
      :model="form"
      :rules="rules"
      ref="form"
      label-width="84px"
      label-suffix="："
    >
      <el-row>
        <el-col :span="4">
          <bda-form-item label="规则分类" prop="projectName">
            <bda-select
              v-model="form.projectName"
              size="small"
              filterable
              clearable
              placeholder="请选择"
              style="width: 100%"
            >
              <bda-option
                v-for="item in projectNameList"
                :key="item.code"
                :label="item.name"
                :value="item.name"
              >
              </bda-option>
            </bda-select>
          </bda-form-item>
        </el-col>
        <el-col :span="4">
          <bda-form-item label="归口部门" prop="team">
            <bda-select
              v-model="form.team"
              filterable
              clearable
              :searchable="false"
              placeholder="请选择"
              style="width: 100%"
              size="small"
            >
              <bda-option
                v-for="item in teamList"
                :title="item.name"
                :key="item.code"
                :label="item.name"
                :value="item.code"
              >
              </bda-option>
            </bda-select>
          </bda-form-item>
        </el-col>
        <el-col :span="4">
          <bda-form-item label="风险等级" prop="projectType">
            <bda-select
              v-model="form.projectType"
              size="small"
              filterable
              clearable
              placeholder="请选择"
              style="width: 100%"
            >
              <bda-option
                v-for="item in projectTypeList"
                :key="item.code"
                :label="item.name"
                :value="item.name"
              >
              </bda-option>
            </bda-select>
          </bda-form-item>
        </el-col>
        <el-col :span="4">
          <bda-form-item label="责任岗位" prop="resJobs">
            <bda-select
              v-model="form.resJobs"
              size="small"
              filterable
              clearable
              placeholder="请选择"
              style="width: 100%"
            >
              <bda-option
                v-for="item in resJobsList"
                :key="item.code"
                :label="item.name"
                :value="item.name"
              >
              </bda-option>
            </bda-select>
          </bda-form-item>
        </el-col>
        <el-col :span="6">
          <el-input
            v-model.trim="form.keyValue"
            placeholder="请输入关键字"
            style="width: 50%; padding-left: 50%"
          >
          </el-input>
        </el-col>
        <el-col :span="2">
          <bda-button @click="serachEvent" style="float: right" type="primary">查询</bda-button>
        </el-col>
      </el-row>
    </el-form>

    <add-dialog ref="addDialogRef" v-if="addDialog"></add-dialog>
    <add-doc-dialog ref="addDocRef"></add-doc-dialog>
    <import-doc-dialog ref="importDocRef"></import-doc-dialog>
  </div>
</template>
<script>
import addDialog from "./dialog/add.vue";
import addDocDialog from "./dialog/addDoc.vue";
import importDocDialog from "./dialog/importDoc.vue";
import Interface from "../../../../base/mixin/enterprise-manage-center";
import axios from "axios";
import { API_PATH } from '@/config'
export default {
  name: "projectManageForm",
  components: { addDialog, addDocDialog, importDocDialog },
  mixins: [Interface],
  data() {
    return {
      //form
      form: {
        projectName: "", //规则分类
        team: "", //归口部门
        projectType: "", //风险等级
        resJobs: "", //责任岗位
        keyValue: "", //关键字
      },

      rules: {},
      teamList: [], //归口部门列表
      projectNameList: [], //规则分类列表
      resJobsList: [],
      projectTypeList: [], //风险等级列表
      addDialog:false
    };
  },
  mounted() {
    this.getSelectList()
  },

  methods: {
    // goBack() {
    //   window.history.go(-1);
    // },
    //获取下拉数据
    getSelectList(){
      this.ruleSelect({}).then((res)=>{
        for (let key in res){
          if (key){
            this.teamList = res['department']||[]
            this.projectTypeList = res['riskLevel']||[]
            this.resJobsList = res['zrgw']||[]
            this.projectNameList = res['ruleType']||[]
          }
        }

      })

    },
    //新增弹窗
    addDialogClick() {
      let form = {
        department:this.teamList,
        zrgw:this.resJobsList,
        riskLevel:this.projectTypeList,
      }
      this.addDialog = true
      this.$nextTick(()=>{
        this.$refs.addDialogRef.openDialog(form);
      })
    },

    //编辑弹窗
    editDialogClick(row){
      let form = {
        department:this.teamList,
        zrgw:this.resJobsList,
        riskLevel:this.projectTypeList,
      }
      this.GetRuleDetail(form,row)
      this.addDialog = true
      // this.$nextTick(()=>{
      //   this.$refs.addDialogRef.openDialog(form,res);
      // })
    },

    //编辑详细数据
    GetRuleDetail(form,row){
      this.getRuleDetail({
        ruleId:row.ruleId
      }).then((res)=>{
        if (res){
          this.$refs.addDialogRef.openDialogEdit(form,res);
        }
      })
    },

    //导出excel
    exportExcel() {
      let path = '/rule/excelExport'
      let data = {
        "departmentId": this.form.team,
        "keyword": this.form.keyValue,
        "riskLevel": this.form.projectType,
        "ruleType": this.form.projectName,
        "zrgw": this.form.resJobs,
      }
      axios({
        method: "POST",
        url: API_PATH + path,
        data:data,
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        },
        responseType: 'blob'
      }).then(res => {
        const content = res.data
        const blob = new Blob([content])
        const fileName = '导出信息.xls'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      }).finally(()=>{
      }).catch(()=>{
      })
    },

    //导入
    importDocClick() {},
    serachEvent() {
      this.$emit('serachEvent',this.form)
    },
    //批量删除
    batchDelete() {
      this.$emit('batchDelete',true)
    },
  },
};
</script>
<style lang="scss" scoped>
.project-manage-form {
  /deep/ {
    .manageBtn {
      margin-bottom: 20px;
    }
    .el-button {
      background-color: #041338;
      border: 1px solid #4389f9;
      border-radius: 4px;
      color: #fff;
    }

    .el-input__inner {
      background: #041338;
      border: 1px solid #4389f9;
      color: #4298f3;
    }
    &.el-input__inner::placeholder {
      color: #4298f3;
    }
    .el-select__caret {
      color: #4298f3;
    }

    .el-form-item__label {
      color: #fff;
    }
  }
}
  .project-manage-formL{
    background: #eef3ff;
    box-sizing: border-box;
    padding: 6px;
    /deep/ {
      .manageBtn {
        margin-bottom: 20px;
      }
      .el-button {
        background-color: #4389f9;
        border: 1px solid #4389f9;
        border-radius: 4px;
        color: #fff;
      }

      .el-input__inner {
        /*background: #041338;*/
        border: 1px solid #cccccc;
        border-radius: 4px;
        /*color: #4298f3;*/
      }
      &.el-input__inner::placeholder {
        color: #4298f3;
      }
      .el-select__caret {
        color: #4298f3;
      }

    }
  }
</style>